export default {
	data() {
		return {
			//热搜榜数据
			hotData: [],
			//默认搜索关键词数据
			defaultText: {},
			//输入框内容
			inputValue: ''
		}
	},
	onLoad() {
		//调用热搜榜数据
		this.getHot()
		//调用默认搜索关键词数据
		this.getDefault()
	},
	methods: {
		//请求热搜榜数据函数
		getHot() {
			this.$request({
				url: '/search/hot/detail'
			}).then(res => {
				this.hotData = res.data
			})
		},
		//请求默认搜索关键词
		getDefault() {
			this.$request({
				url: '/search/default'
			}).then(res => {
				this.defaultText = res.data
			})
		},
		//传递热搜点击事件
		hotItemClick(item) {
			uni.navigateTo({
				url: `/pages/discover/discover?keywords=${item.searchWord}`
			})
		},
		searchClick() {
			const reg = "^[ ]+$";
			const re = new RegExp(reg);

			if (!this.inputValue || this.inputValue == '' || re.test(this.inputValue)) {
				uni.showToast({
					title: '没有内容',
					icon: 'none',
					duration: 2000
				});
			} else {
				let v = this.inputValue.replace(/\s/g, '')
				uni.navigateTo({
					url: `/pages/discover/discover?keywords=${v}`
				})
			}
		}
	}
}
